import React from 'react';

// components
import DropDownDataBox from '../../reuseable/DropDownDataBox';
import DropDownDataStudent from '../../reuseable/DropDownDataStudent';

const Syllabus = ({ userRole }) => {

    const testText = '                                             ...       .                                '

    return (<>
                { userRole === 'coach' &&
                <div className='flex flex-col gap-y-7'>
                    <DropDownDataBox title={" "} dataText={testText}  />
                    <DropDownDataBox title={" "} dataText={testText} />
                    <DropDownDataBox title={" "} dataText={testText} />
                    <DropDownDataBox title={"  "} dataText={testText} />
                </div>
                }

                { userRole === 'student' &&
                <div className='flex flex-col gap-y-7'>
                    <DropDownDataStudent title={" "} id={'1'} dataText={testText}  />
                </div>
                }
            </>
    );
};

export default Syllabus;